import React from "react";

import LinkIcon from "@mui/icons-material/Link";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import { get } from "lodash";

import SwitchToggle from "../../ui-components/switchToggle/SwitchToggle";
const SocialLinksData = (props) => {
  const { consultantInfo, loading, infoLoading, handleSectionChildStatus } =
    props;
  return (
    <>
      {get(consultantInfo, "socialMediaInfo", []).map((item, index) => (
        <Box key={index}>
          <Stack
            direction="row"
            alignItems="center"
            justifyContent="space-between"
            spacing={1}
            mb={1}
          >
            <Stack direction="row" alignItems="center" spacing={1}>
              <Box component="span">
                <Avatar
                  sx={{
                    backgroundColor: "grey.300",
                    color: "text.secondary",
                  }}
                >
                  <LinkIcon />
                </Avatar>
              </Box>
              <Box component="div">
                <Typography
                  variant="body2"
                  color="text.primary"
                  className="fw-bold"
                >
                  Follow me on {item.socialMediaChanel}
                </Typography>
                <Typography
                  color="primary.main"
                  variant="caption"
                  component="span"
                  sx={{ wordBreak: "break-all" }}
                >
                  {item.chanelLink}
                </Typography>
              </Box>
            </Stack>
            <SwitchToggle
              checked={get(item, "status", true)}
              onChange={(e) =>
                handleSectionChildStatus(e, "socialMediaInfo", item._id)
              }
              disabled={
                loading ||
                infoLoading ||
                !get(consultantInfo, "socialMediaInfoStatus", false)
              }
              inputProps={{
                "aria-label": `socialMediaInfo-${index}-status`,
                role: "switch",
              }}
            />
          </Stack>
        </Box>
      ))}
    </>
  );
};

export default SocialLinksData;
