import React from "react";

import BusinessCenterOutlinedIcon from "@mui/icons-material/BusinessCenterOutlined";
import Box from "@mui/material/Box";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";

import { get, isEmpty } from "lodash";

import SwitchToggle from "../../ui-components/switchToggle/SwitchToggle";
import moment from "moment";

const ExperienceData = (props) => {
  const { consultantInfo, loading, infoLoading, handleSectionChildStatus } =
    props;

  return (
    <>
      {get(consultantInfo, "work", []).map((item, index) => (
        <Box key={index}>
          <Stack
            direction="row"
            align="center"
            justifyContent="space-between"
            spacing={2}
            py={2}
          >
            <Stack direction="row" align="center">
              <Box component="span">
                <Avatar
                  sx={{
                    backgroundColor: "grey.300",
                    color: "text.secondary",
                  }}
                >
                  <BusinessCenterOutlinedIcon />
                </Avatar>
              </Box>
              <Box sx={{ textAlign: "start", marginLeft: "10px" }}>
                <Typography
                  variant="body2"
                  color="text.primary"
                  className="fw-bold"
                >
                  {`${item.title} , ${item.companyName} at ${item.employmentType}`}
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  {moment(item.startDate).format("L")}
                  {!isEmpty(item.endDate)
                    ? +" to " + moment(item.endDate).format("L")
                    : null}{" "}
                  {"at " + item.location}
                </Typography>
              </Box>
            </Stack>
            <SwitchToggle
              checked={get(item, "status", true)}
              onChange={(e) => handleSectionChildStatus(e, "work", item._id)}
              disabled={
                loading ||
                infoLoading ||
                !get(consultantInfo, "workStatus", false)
              }
              inputProps={{
                "aria-label": `work-${index}-status`,
                role: "switch",
              }}
            />
          </Stack>
        </Box>
      ))}
    </>
  );
};

export default ExperienceData;
