import React from "react";

import Avatar from "@mui/material/Avatar";
import Box from "@mui/material/Box";
import LinearProgress from "@mui/material/LinearProgress";
import Stack from "@mui/material/Stack";
import Switch from "@mui/material/Switch";
import Rating from "@mui/material/Rating";
import Typography from "@mui/material/Typography";

import ProfileIcon from "../../../assets/images/user.svg";
import CMDataGrid from "../../ui-components/data-grid/CMDataGrid";
import CMDataGridToolbar from "../../ui-components/data-grid/CMDataGridToolbar";
import EmptyTableRowOverlay from "../../ui-components/data-grid/EmptyTableRowOverlay";

const DateTime = () => {
  var today = new Date();

  var date =
    today.getDate() + "/" + (today.getMonth() + 1) + "/" + today.getFullYear();

  var time = today.toLocaleString("en-US", {
    hour: "numeric",
    minute: "numeric",
    hour12: true,
  });

  var dateTime = date + " " + time;
  return dateTime;
};

const TableRow = [
  {
    id: 1,
    reviewer: {
      avatar: ProfileIcon,
      name: "theguthealthdoctor",
      location: "San, Francisco",
    },
    review: {
      comment:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam eu turpis Sed dignissim.....",
    },
    DateTime: DateTime(),
    Status: "asdas",
  },
  {
    id: 2,
    reviewer: {
      avatar: ProfileIcon,
      name: "theguthealthdoctor",
      location: "San, Francisco",
    },
    review: {
      comment:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam eu turpis Sed dignissim.....",
    },
    DateTime: DateTime(),
    Status: "asdas",
  },
  {
    id: 3,
    reviewer: {
      avatar: ProfileIcon,
      name: "theguthealthdoctor",
      location: "San, Francisco",
    },
    review: {
      comment:
        "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam eu turpis Sed dignissim.....",
    },
    DateTime: DateTime(),
    Status: "asdas",
  },
];

const TableColumns = [
  {
    field: "reviewer",
    headerName: "Reviewer",
    width: 250,
    renderCell: (params) => (
      <Stack direction="row" spacing={2}>
        <Avatar alt="Remy Sharp" src={params.value.avatar} />
        <Box>
          <Typography variant="body1" fontWeight="500" color="text.primary">
            {params.value.name}
          </Typography>
          <Typography variant="caption" color="grey[600]">
            {params.value.location}
          </Typography>
        </Box>
      </Stack>
    ),
  },
  {
    field: "review",
    headerName: "Review",
    width: 450,
    renderCell: (params) => (
      <>
        <Stack direction="row" spacing={2}>
          <Box>
            <Rating name="no-value" value={null} />
            <Typography
              variant="body2"
              sx={{ wordWrap: "break-word" }}
              color="grey[600]"
            >
              {params.value.comment}
              <Typography
                component="span"
                color="primary.main"
                sx={{ textTransform: "capitalize" }}
              >
                Read more
              </Typography>
            </Typography>
          </Box>
        </Stack>
      </>
    ),
  },
  {
    field: "DateTime",
    headerName: "Date & Time",
    width: 200,
  },
  {
    field: "Status",
    headerName: "Status",
    width: 100,
    renderCell: (params) => (
      <Box>
        <Switch />
      </Box>
    ),
  },
];

export default function ReviewList() {
  const CustomToolbar = () => {
    return <CMDataGridToolbar align="right" divider={true} />;
  };

  return (
    <div style={{ height: 500, width: "100%" }}>
      <CMDataGrid
        sx={{
          border: 1,
          borderColor: "#dedede",
          bgcolor: "#fff",
          boxShadow: "none",
        }}
        rows={TableRow}
        columns={TableColumns}
        components={{
          Toolbar: CustomToolbar,
          NoRowsOverlay: EmptyTableRowOverlay,
          LoadingOverlay: LinearProgress,
        }}
      />
    </div>
  );
}
